/* Global Styles */
body {
  font-family: 'Red Hat Display', sans-serif;
  margin: 0;
  padding: 0;
}
/* Container for the Hero Section */
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 4rem;
  max-width: 100%;
  margin: auto;
  padding: 4rem;
  position: relative;
  z-index: 10;
  /* Ensure content is above particles */
}

/* Top Content (Hero Text and Buttons) */
.top-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 100%;
  position: relative;
  z-index: 10;
  /* Ensure top content is above particles */
}

/* Hero Text Styling */
.hero-text {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  gap: 0.5rem;
}

.hero-text h1 {
  font-size: 5rem;
  color: #ffffff;
  /* Neon Cyan */
  font-weight: 400;
  margin: 0;
  white-space: nowrap;
  text-shadow: 0 0 10px #000000, 0 0 1px #000000;
  /* Glow Effect */
}

.hero-text p {
  font-size: 1.5rem;
  color: #fff;
  font-weight: 400;
  margin: 0;
  white-space: nowrap;
}

/* Button Container */
.button-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
  margin-top: 1rem;
}

/* Button Styling */
.button {
  padding: 1.1rem;
  background-color: #000000;
  /* Neon Cyan */
  color: #ffffff;
  border-color: white;
  border-radius: 40px;
  font-size: 1.6rem;
  font-family: 'Red Hat Display', sans-serif;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.button:hover {
  transform: translateY(-5px);
  box-shadow: 0 0 10px #00f7ff, 0 0 40px #00f7ff;
  /* Glow Effect */
}

/* Social Media Icons */
.social-media img {
  width: 5rem;
  height: 4rem;
  padding: 0.7rem;
  transition: transform 0.3s ease;
}

.social-media img:hover {
  transform: scale(1.1);
}

/* Boxes Container (Projects Section) */
.boxes-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  width: 100%;
  margin-top: 2rem;
  position: relative;
  z-index: 10;
  /* Ensure boxes are above particles */
}
.blank-paragraph {
  margin: 0;
  /* Remove default margin */
  height: 1rem;
  /* Set a fixed height for consistent spacing */
}
/* Individual Project Box */
.box {
  background-color: #ffffff;
  border: 1px solid #000000;
  /* Neon Cyan */
  border-radius: 10px;
  width: 300px;
  padding: 20px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  animation: fadeIn 0.8s ease-out;
  display: flex;
      /* Make the box a flex container */
  flex-direction: column;
      /* Stack content vertically */
}

/* Alternate Box Styles */
.box.alternate {
  background-color: #000000;
  border: 1px solid #ffffff;
}

.box.alternate .box-title,
.box.alternate .box-content {
  color: #ffffff;
}

.box.alternate .line {
  background-color: #ffffff;
}

.box.alternate .sub {
  color: #666666;
}
.box:hover {
  transform: translateY(-15px);
  box-shadow: 0 0 10px #00f7ff, 0 0 40px #00f7ff;
  /* Glow Effect */
}

/* Box Title and Icons */
.box-title-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.box-title {
  font-size: 1.5rem;
  color: #000000;
  /* Neon Cyan */
  font-weight: 500;
}

.box-icons img {
  width: 30px;
  height: 30px;
  margin-left: 0.5rem;
}

/* Line Separator */
.line {
  width: 100%;
  height: 1px;
  background-color: #000000;
  /* Neon Cyan */
  margin: 1rem 0;
}

/* Subtitle (Technologies Used) */
.sub {
  font-size: 1rem;
  color: #989898;
  margin-bottom: 1rem;
  flex-shrink: 0;
      /* Prevent subtitle from shrinking */
}

/* Box Content (Project Description) */
.box-content {
  font-size: 1rem;
  color: #000000;
  line-height: 1.5;
  flex-grow: 1;
    /* Make description take up remaining space */
}

/* Rainbow Text Animation for Demo Links */
.rainbow {
  background: linear-gradient(90deg, #ff00ff, #00f7ff, #ff00ff, #00f7ff);
  background-size: 200% auto;
  color: #000;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: rainbow-text 3s linear infinite;
}

@keyframes rainbow-text {
  0% {
    background-position: 0 0;
  }

  100% {
    background-position: 200% 0;
  }
}

.rainbow-text_animated {
  font-size: 1.2rem;
  font-weight: 500;
  margin-top: 1rem;
  cursor: pointer;
}